.container {
  width: 100%;
  padding-bottom: 100%;
  border: 1px solid black;
  background-image: url('../../assets/images/gameField.jpg');
  position: relative;
  border: 8px solid #0d0702da;
  border-radius: 10px;
  box-sizing: content-box;
  visibility: hidden;
}
.wonDiv {
  position: absolute;
  color: #fff;
  background-color: #000000aa;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.winerContainer {
  width: 80%;
  height: 50%;
  background-color: green;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: Georgia, serif;
  font-size: 24px;
  text-align: center;
  padding: 10px;
}
.steps {
  color: blue;
}
